ul, ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

ul.check-list{
	list-style: none;
	margin: 0 0 1.5em 2rem;
	li:before{
		content: '\2713';
		display: inline-block;
		padding-right: 0.5rem;
	}
}